.App {
  max-width: 860px;
  margin: 40px auto;
}

body {
  background-color: var(--secondaryColor);
}

:root {
  --mainColor: #9fa9a3;
  --mainColorLight: #c5d5c5;
  --secondaryColor: #e3e0cc;
  --textColor: #3b3a30;
}

button {
  background: none;
  border: 2px solid var(--mainColor);
  padding: 5px 10px;
  border-radius: 5px;
  color: var(--textColor);
  cursor: pointer;
  font-size: 1rem;
}

button:hover {
  background: var(--mainColorLight);
}

.card-grid {
  display: grid;
  margin-top: 40px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 20px;
  row-gap: 20px;
}